@keyframes grow {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}


.desktop {
  padding-top: 50px;
}

.header {
  display: flex;
  align-items: center;
  margin: 20px 10px;
  img {
    width: 150px;
  }
  h1 {
    color: $white;
    line-height: 2rem;
  }
}

.card {
  position: relative;
  background-color: $white;
  padding: 20px;
  margin: 50px 10px;
  border-radius: 8px;
  background: url("../../../public/images/bg_paper.jpg");
  background-size: cover;
  background-position: center;
  animation: grow 0.3s cubic-bezier(0.175, 0.885, 0.43, 1.65), 0s 1;
  box-shadow: 7px 7px 0px -1px rgba(255, 255, 255, 0.75);

  .paperclip {
    position: absolute;
    top: -16px;
    right: 2px;
    width: 40px;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
  }

  .category {
    position: absolute;
    top: -15px;
    left: 90px;
    background-color: #f72585;
    padding: 10px 30px;
    transform: rotate(-5deg);
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-radius: 2px;
  }

  .geography{
    position: absolute;
    top: -15px;
    left: 90px;
    background-color: #f7a325;
    padding: 10px 30px;
    transform: rotate(-5deg);
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-radius: 2px;
  }

  .animal{
    position: absolute;
    top: -15px;
    left: 90px;
    background-color: #8338ec;
    color: $white;
    padding: 10px 30px;
    transform: rotate(-5deg);
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-radius: 2px;
  }

  .funny{
    position: absolute;
    top: -15px;
    left: 90px;
    background-color: #8ac926;
    padding: 10px 30px;
    transform: rotate(-5deg);
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-radius: 2px;
  }

  .food{
    position: absolute;
    top: -15px;
    left: 90px;
    background-color: #b5179e;
    color: $white;
    padding: 10px 30px;
    transform: rotate(-5deg);
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-radius: 2px;
  }

  .history{
    position: absolute;
    top: -15px;
    left: 90px;
    background-color: #fb5607;
    padding: 10px 30px;
    transform: rotate(-5deg);
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-radius: 2px;
  }

  .sport{
    position: absolute;
    top: -15px;
    left: 90px;
    background-color: #3d5a80;
    padding: 10px 30px;
    transform: rotate(-5deg);
    color: $white;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-radius: 2px;
  }

  .art{
    position: absolute;
    top: -15px;
    left: 90px;
    background-color: #3a86ff;
    padding: 10px 30px;
    transform: rotate(-5deg);
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-radius: 2px;
  }

  p {
    margin: 50px 0;
    line-height: 1.8rem;
  }

  .btn-push {
    cursor: pointer;
    position: relative;
    top: 0px;
    font-family: "Open Sans";
    text-decoration: none;
    font-size: 37px;
    background: #f7f7f7;
    margin: 0 10px;
    border: 1px solid #c4c4c4;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 50px;
    height: 50px;
    width: 50px;
    -webkit-box-shadow: 0px 5px 0px #c4c4c4;
    -moz-box-shadow: 0px 5px 0px #c4c4c4;
    -ms-box-shadow: 0px 5px 0px #c4c4c4;
    -o-box-shadow: 0px 5px 0px #c4c4c4;
    box-shadow: 0px 5px 0px #c4c4c4;
    color: #222;
    text-shadow: 1px 1px 1px #fff;
    -webkit-transition: All 250ms ease;
    -moz-transition: All 250ms ease;
    -o-transition: All 250ms ease;
    -ms-transition: All 250ms ease;
    transition: All 250ms ease;
  }
  .btn-push:active {
    position: relative;
    top: 5px;
    -webkit-box-shadow: none !important;
    -moz-box-shadow: none !important;
    -ms-box-shadow: none !important;
    -o-box-shadow: none !important;
    box-shadow: none !important;
    -webkit-transition: All 250ms ease;
    -moz-transition: All 250ms ease;
    -o-transition: All 250ms ease;
    -ms-transition: All 250ms ease;
    transition: All 250ms ease;
  }
}

.footer {
  text-align: center;
  color: $white;
  font-size: 0.7rem;
}
